___CSS_LOADER_EXPORT___.push([module.id, `body {
  background-color: grey;
}

.container {
  width: 100%;
  height: 100%;
  margin-top: 10vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container i {
  font-size: 24px;
  cursor: pointer;
  padding: 0 20px;
}

.carousel {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  width: 80vw;
  overflow: hidden;
  transition: transform 0.5s cubic-bezier(0.77, 0, 0.18, 1);
}
.carousel-item {
  width: 120px;
  height: 90px;
  object-fit: cover;
  transition:
    transform 0.3s,
    width 0.3s,
    height 0.3s;
  opacity: 0.7;
  border-radius: 10px;
}
.carousel-item.active {
  width: 240px;
  height: 180px;
  transform: scale(1.2);
  opacity: 1;
  z-index: 1;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
}

.selectors {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
}
.circle {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: white;
  cursor: pointer;
}
.circle.active {
  background-color: #333;
  border: 2px solid #fff;
}
